0 5px 15px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);\n  padding: 14px 40px;\n  font-size: 16px;\n  cursor: pointer;\n\n  &:active {\n    transform: scale(0.98);\n  }\n\n  &:focus {\n    outline: 0;\n  }\n}\n","@use 'sass:math';\n\n/* https://sass-lang.com/documentation/modules/math */\n/* math.div($number1, $number2) //=> number  */\n\n/* https://sass-lang.com/documentation/at-rules/function */\n// divide $pixel by 16 to return rem unit\n@function rem($pixel) {\n  // throw an error when parameter includes 'px' string or any unit\n  @if math.is-unitless($pixel)  {\n    @return math.div($pixel, 16) + rem;\n  } @else {\n    @error \"Property #{$pixel} must be unitless when using rem function. Only numbers please.\";\n    // @return unit($pixel);\n  }\n}\n\n// divide $pixel by 16 to return em unit\n@function em($pixel) {\n  // throw an error when parameter includes 'px' string or any unit\n  @if math.is-unitless($pixel)  {\n    @return math.div($pixel, 16) + em;\n  } @else {\n    @error \"Property #{$pixel} must be unitless when using em function. Only numbers please.\";\n    // @return unit($pixel);\n  }\n}\n\n\n","@use 'functions' as f;\n\n// 700px, 900px, 1440px\n$breakpoints-up: (\n    'medium': f.em(700), /* 43.75em */\n    'large': f.em(900), /* 56.25em */\n    'xlarge': f.em(1440), /* 90em */\n);\n\n// 699.98px, 899.98px, 1439.98px\n$breakpoints-down: (\n    'small': f.em(699.98), /* 43.7485em */\n    'medium': f.em(899.98), /* 56.24875em */\n    'large': f.em(1439.98), /* 89.99875em */\n);\n\n// mixins defacto\n@mixin breakpoint($size) {\n    @media (min-width: map-get($breakpoints-up, $size)) {\n        @content;\n    }\n}\n\n// special cases - styling for mobile scenarios\n@mixin breakpoint-down($size) {\n    @media (max-width: map-get($breakpoints-down, $size)) {\n        @content;\n    }\n}\n\n// ! Order of breakpoints in cascade matters !\n// --------------------------------FIN------------------------------------------\n\n/* .02 px difference - inspired by Bootstrap & codercoder */\n/* rem for media query seems to be buggy with safari as of 20220427214236 */\n","// sass variables - not relevant as css custom variables can be modified with JavaScript\n@font-face {\n  font-family: \"Work Sans\";\n  font-style: normal;\n  font-weight: normal;\n  src: url(\"../fonts/Work_Sans/WorkSans-VariableFont_wght.ttf\") format(\"truetype\");\n}\n\n\n// @font-face {\n//   font-family: \"Work Sans\";\n//   font-style: italic;\n//   font-weight: normal;\n//   src: url(\"../fonts/Work_Sans/WorkSans-Italic-VariableFont_wght.ttf\") format(\"truetype\");\n// }\n\n@font-face {\n  font-family: \"Parisienne\";\n  font-style: cursive;\n  font-weight: normal;\n  src: url(\"../fonts/Parisienne/Parisienne-Regular.ttf\") format(\"truetype\");\n}\n\n$font-sans: \"Work Sans\", Helvetica, Arial, sans-serif;\n$font-cursive: \"Parisienne\", cursive;\n\n// -----------------------------------------------------------------------------\n\n// :root {\n//   /* font-family */\n//   --ff-cursive: \"Parisienne\", cursive;\n//   --ff-sans: \"Work Sans\", Helvetica, Arial, sans-serif;\n// }\n","@use \"../util\" as u; /* namespacing utils as u */\n\nhtml {\n  box-sizing: border-box;\n  font-size: 100%;\n\n  text-size-adjust: none;\n  -webkit-text-size-adjust: none;\n}\n\n/* 1. Use a more-intuitive box-sizing model. */\n*,\n*::before,\n*::after {\n  box-sizing: inherit;\n}\n\nbody {\n  min-height: 100vh;\n  margin: 0;\n  // padding-top: unset; /* enable this */\n\n  // font-family: var(--ff-sans);\n  font-family: u.$font-sans;\n  font-size: var(--fs-400, 23px);\n  line-height: 1.5;\n  font-weight: 400;\n  text-rendering: optimizeSpeed;\n  -webkit-font-smoothing: antialiased;\n\n  color: var(--mdc-theme-on-background);\n  background-color: var(--mdc-theme-success-container);\n\n  // display: grid;\n  // grid-template-rows: auto 1fr auto;\n\n  // background-color: var(--mdc-theme-background, #fcfcfc); /* enable later */\n  /* overflow-x: hidden; */\n}\n\nbody