ustom */\n  --mdc-theme-on-surface--morph-bg: hsla(9, 12%, 11%, 0.03);\n\n  /* color - read-only/surface/light */\n  --mdc-theme-surface1: linear-gradient(\n      0deg,\n      rgba(156, 65, 61, 0.05),\n      rgba(156, 65, 61, 0.05)\n    ),\n    #fcfcfc; /* +5% Primary */\n  --mdc-theme-surface2: linear-gradient(\n      0deg,\n      rgba(156, 65, 61, 0.08),\n      rgba(156, 65, 61, 0.08)\n    ),\n    #fcfcfc; /* +8`% Primary */\n  --mdc-theme-surface3: linear-gradient(\n      0deg,\n      rgba(156, 65, 61, 0.11),\n      rgba(156, 65, 61, 0.11)\n    ),\n    #fcfcfc; /* +11% Primary */\n  --mdc-theme-surface4: linear-gradient(\n      0deg,\n      rgba(156, 65, 61, 0.12),\n      rgba(156, 65, 61, 0.12)\n    ),\n    #fcfcfc; /* +12% Primary */\n  --mdc-theme-surface5: linear-gradient(\n      0deg,\n      rgba(156, 65, 61, 0.14),\n      rgba(156, 65, 61, 0.14)\n    ),\n    #fcfcfc; /* +14% Primary */\n\n  /* shadow */\n  --mdc-theme-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2),\n    0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);\n  --mdc-theme-shadow-on-text: 0 2px 4px rgb(0 0 0 / 12%);\n}\n","/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */\n\n/* Document\n   ========================================================================== */\n\n/**\n * 1. Correct the line height in all browsers.\n * 2. Prevent adjustments of font size after orientation changes in iOS.\n */\n\nhtml {\n  line-height: 1.15; /* 1 */\n  -webkit-text-size-adjust: 100%; /* 2 */\n}\n\n/* Sections\n   ========================================================================== */\n\n/**\n * Remove the margin in all browsers.\n */\n\nbody {\n  margin: 0;\n}\n\n/**\n * Render the `main` element consistently in IE.\n */\n\nmain {\n  display: block;\n}\n\n/**\n * Correct the font size and margin on `h1` elements within `section` and\n * `article` contexts in Chrome, Firefox, and Safari.\n */\n\nh1 {\n  font-size: 2em;\n  margin: 0.67em 0;\n}\n\n/* Grouping content